import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class AddAreaService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    addArea(body) {
        // let body = JSON.stringify({ CityId: cityId, AreaName: AreaName });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "City/addArea", body, options)
            .map(res => res.json());
    }
    getAreaName(CityId) {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });
        return this.http.get(this.urlService.baseUrl + "City/getAllAreasByCityId?CityId=" + CityId, options)
            .map(res => res.json());
    }
    addCity(CityName) {
        console.log(CityName);
        let body = JSON.stringify({ "Name": CityName });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });
        return this.http.post(this.urlService.baseUrl + "City/addCity", body, options)
            .map(res => res.json());
    }
    deleteCity(cityId) {
        return this.http.get(this.urlService.baseUrl + "City/removeCity?cityId=" + cityId)
            .map(res => res.json());
    }
    deleteArea(areaId) {
        return this.http.get(this.urlService.baseUrl + "City/removeArea?areaId=" + areaId)
            .map(res => res.json());
    }
    updateCity(Id, CityName) {
        let body = JSON.stringify({ "Id":Id, "Name": CityName });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });
        return this.http.post(this.urlService.baseUrl + "City/editCity", body, options)
            .map(res => res.json());
    }
    updateArea(body) {
        // let body = JSON.stringify({ "Id": Id, "Name": CityArea });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });
        return this.http.post(this.urlService.baseUrl + "City/editArea", body, options)
            .map(res => res.json());
    }

    getUserAddressById(addressId) {

        let headers = new Headers();

        headers.append('Content-Type', 'application/json; charset=UTF-8');
        return this.http.get(this.urlService.baseUrl + "User/getAddressByAddressId?addressId=" + addressId, { headers: headers })
            .map(res => res.json());
    }
    
}


