import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class MeasurementAndDosageSizesService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    addMeasurementService(MeasurementName, MeasurementSymbol) {
        let body = JSON.stringify({ measurementName: MeasurementName, measurementSymbol: MeasurementSymbol });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addMeasurement", body, options)
            .map(res => res.json());
    }
    addDosageAndSizeService(DosageName, DosageSymbol) {
        let body = JSON.stringify({ UnitName: DosageName, UnitSymbol: DosageSymbol });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addUnit", body, options)
            .map(res => res.json());

    }
    updateMeasurement(Id, measurementName, measurementSymbol) {
        let body = JSON.stringify({ Id: Id, measurementName: measurementName, measurementSymbol: measurementSymbol });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/editMeasurement", body, options)
            .map(res => res.json());
    }
    deleteMeasurement(Id) {
        return this.http.get(this.urlService.baseUrl + "Chemist/deleteMeasurement?id=" + Id)
            .map(res => res.json());
    }
    getMeasurements() {
        return this.http.get(this.urlService.baseUrl + "Chemist/getAllChemistMeasurements")
            .map(res => res.json());
    }
    getDosage() {
        return this.http.get(this.urlService.baseUrl + "Chemist/getAllChemistUnits")
            .map(res => res.json());
    }
    getDosageSizes(Id) {
        return this.http.get(this.urlService.baseUrl + "Chemist/getDosageAndSizeValuesByDosageAndSizeId?dosageAndSizeId=" + Id)
            .map(res => res.json());
    }
    deleteDosage(Id) {
        return this.http.get(this.urlService.baseUrl + "Chemist/deleteDosage?Id=" + Id)
            .map(res => res.json());
    }
    updateDosage(Id, Name, Symbol) {
        let body = JSON.stringify({ Id: Id, UnitName: Name, UnitSymbol: Symbol });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/editDosage", body, options)
            .map(res => res.json());
    }
    deleteDosageValue(Id) {
        return this.http.get(this.urlService.baseUrl + "Chemist/deleteDosageValue?Id=" + Id)
            .map(res => res.json());
    }
    updateDosageValue(Id, Value) {
        console.log(Id);
        console.log(Value);
        let body = JSON.stringify({ Id: parseFloat(Id), Value: parseFloat(Value) });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/editDosageValues", body, options)
            .map(res => res.json());
    }
    addDosageValues(Id, dosage_value) {
        let body = JSON.stringify({ DosageAndSizesId: Id, Value: dosage_value });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addUnitValue", body, options)
            .map(res => res.json());

    }


    // manufacturing
    addManufacturing(body) {

        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "admin/addManufacturer", body, options)
            .map(res => res.json());

    }

    getAllManufactures(){

        return this.http.get(this.urlService.baseUrl + "admin/getAllManufacturers")
            .map(res => res.json());
    }

    updateManufacture(body){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "admin/editManufacturer", body, options)
            .map(res => res.json());

    }

    DeleteManufacture(Id) {
        return this.http.get(this.urlService.baseUrl + "admin/removeManufacturer?manufacturerId=" + Id)
            .map(res => res.json());
    }


    addLogistic(body){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/addPLUser", body, options)
            .map(res => res.json());

    }
    updateLogistic(body){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/addPLUser", body, options)
            .map(res => res.json());
    }
    AllLogistics(){
        return this.http.get(this.urlService.baseUrl + "Logistics/getAllLogistics")
        .map(res => res.json());
    }

}


