import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class AddchemistService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    addChemist(getArea_Id, ChemistEmail, PharmaCommission, CityId, ChemistName, Address, Longitude, latitide, ChemistId, PharmacyName) {
        console.log("service is caling");
        let body = JSON.stringify({ PharmaCommission: PharmaCommission,Address:Address, AreaId: getArea_Id, Email: ChemistEmail,ContactPersonName: ChemistName, CityId: CityId,Longitude: Longitude, Latitude: latitide, ChemistId: ChemistId, CompanyName:PharmacyName });
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addPharmaicst", body, options)
            .map(res => res.json());
    }
    addChemistManually(PharmacyName,getArea_Id, ChemistEmail, PharmaCommission,Address, CityId,  ChemistName, Longitude, latitide, PhoneNumber) {
        let body = JSON.stringify({PharmacyName:PharmacyName, PharmaCommission: PharmaCommission,Address:Address, AreaId: getArea_Id, Email: ChemistEmail,ContactPersonName: ChemistName, CityId: CityId,Longitude: Longitude, Latitude: latitide, PhoneNumber:PhoneNumber });
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addPharmaicstManually", body, options)
            .map(res => res.json());
    }
    updateChemist(getArea_Id, PharmaCommission, CityId, ChemistName, Longitude, latitide, ChemistId, Address) {
        let body = JSON.stringify({ PharmaCommission: PharmaCommission, AreaId: getArea_Id, CityId: CityId, PharmacyName: ChemistName, Longitude: Longitude, Latitude: latitide, ChemistId: ChemistId, Address:Address });
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/updateChemist", body, options)
            .map(res => res.json());
    }
    blockUnBlockChemist(userId) {
        return this.http.get(this.urlService.baseUrl + "User/changeBlockStatusByUserId?userId=" + userId)
            .map(res => res.json());
    }

    getUserByGUID(guid){
        return this.http.get(this.urlService.baseUrl + "User/getUserByGUID?guid=" + guid)
            .map(res => res.json());
    }
    addUserAfterAuth(body){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/addUserCredentials", body, options)
            .map(res => res.json());
    }
}









