import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class LoginService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    login(userName, password) {
        let body = JSON.stringify({ UserName: userName, Password: password, UserRole: 1 });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/login", body, options)
            .map(res => res.json());
    }
    chemistloginService(Chemistusername, Chemistpassword) {
        let body = JSON.stringify({ UserName: Chemistusername, Password: Chemistpassword, UserRole: 2 });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "User/login", body, options)
            .map(res => res.json());

    }
    forgotPassword(Email) {
        return this.http.get(this.urlService.baseUrl + "User/forgotPassword?email=" + Email)
            .map(res => res.json());
    }

}


